.works {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  // padding-bottom: 40px;
}

.works__text {
  text-align: center;
}

.works__list {
  @include list-reset();

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 20px;
  padding: 0 30px;

  @media (min-width: $tablet) {
    flex-direction: row;
    // justify-content: center;
    justify-content: space-between;
    padding: 0 50px;
  }

  @media (min-width: $desktop) {
    margin-top: 40px;
  }
}

.works__item {
  position: relative;

  flex-shrink: 1;
  width: 100%;
  min-height: 200px;
  margin-bottom: 40px;
  overflow: hidden;

  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.4);


  &:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }

  &:hover .project-description {
    display: block;

    animation: slide-left 0.2s;
  }

  &--technomarkt {
    background-image: url("../img/technomart-preview.jpg");
  }

  &--mishka {
    background-image: url("../img/mishka-preview.jpg");
  }

  &--keksobooking {
    background-image: url("../img/keksobooking_preview.jpg");
  }

  &--hostels {
    background-image: url("../img/hostels-share.jpg");
  }

  &--karlson {
    background-image: url("../img/karlson-share.jpg");
  }

  &--booking {
    background-image: url("../img/apartment-share.jpg");
  }

  @media (min-width: $tablet) {
    // margin-left: 80px;
    width: 270px;
    min-height: 250px;

    &:first-child {
      margin-left: 0;
    }
  }

  @media (min-width: $desktop) {
    width: 400px;
    min-height: 225px;
  }
}

.works__descr {
  margin: 0;
  margin-top: 35px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.works__link {
  position: relative;
  z-index: 3;

  display: inline-block;
  width: 100%;
  height: 100%;
  padding-top: 60px;

  text-align: center;
  color: transparent;

  background: transparent;


  &:hover,
  &:focus {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }
}
