.container-header {
  // max-width: 100%;
  max-width: 1920px;
}

.maintenance {
  position: relative;

  display: flex;
  flex-direction: column;


  @media (min-width: $tablet) {
    flex-direction: row;
  }
}

.maintenance__logo {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet) {
    width: 100%;
    // height: 500px;
  }
}

.maintenance__logo-img {
  width: 100%;
  height: auto;
  max-height: 900px;
  margin: 0;
  margin-bottom: -6px;
  padding: 0;
}

.maintenance__logo-pattern {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;

  background-image: url("../../img/pattern.png");
  background-repeat: repeat;
}

.maintenance__descr {
  position: absolute;
  top: 60px;

  display: flex;
  flex-direction: column;
  align-self: center;
  width: 100%;
  padding: 0 15px;

  color: white;

  @media (min-width: $tablet) {
    top: 200px;
  }

  @media (min-width: $desktop) {
    top: 300px;
  }
}

.maintenance__text {
  margin: 0 auto;

  font-size: 14px;

  @media (min-width: $desktop) {
    font-size: 19px;
  }
}

.maintenance__title {
  margin: 5px auto;

  font-size: 19px;
  text-transform: uppercase;

  &--hide {
    visibility: hidden;
    opacity: 0;

    // transition: 1.5s opacity, 1.5s visibility;
  }

  &--show {
    visibility: visible;
    opacity: 1;

    transition: 2s opacity, 2s visibility;
  }

  &::after {
    border: none;
  }

  @media (min-width: $tablet) {
    font-size: 30px;
  }

  @media (min-width: $desktop) {
    margin: 20px auto;

    font-size: 45px;
  }
}

.maintenance__letter {
  &--hide {
    visibility: hidden;
    opacity: 0;
  }

  &--show {
    visibility: visible;
    opacity: 1;

    transition: 2s opacity, 2s visibility;
  }
}
