.project-description {
  position: absolute;
  top: 0;
  left: 0;

  display: none;
  width: 100%;
  height: 100%;
  padding-right: 50px;
  padding-left: 20px;

  background-color: rgba(255,255,255,0.9);
  // transform: translateX(0%) translateY(100%) translateZ(0px);

  // transition: transform 0.2s ease-in-out 0s;
}

.project-description__title {
  margin: 0;
  margin-top: 20px;
  margin-bottom: 15px;
  padding: 0;
}

.project-description__main {
  margin-bottom: 10px;

  font-size: 13px;
  line-height: 1.2;

  @media (min-width: $desktop) {
    font-size: 16px;
  }
}

.project-description__target {
  font-size: 12px;
  line-height: 1.3;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}
