.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-right: 20px;
  padding-bottom: 40px;
  padding-left: 20px;

  background-color: #f5f5f5;

  @media (min-width: $tablet) {
    padding-right: 80px;
    padding-left: 80px;
  }
}

.skills__list {
  @include list-reset();

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  // width: 300px;
  margin-top: 40px;

  @media (min-width: $tablet) {
    justify-content: space-between;
    width: 100%;
  }

  @media (min-width: $desktop) {
    margin-top: 30px;
  }
}

.skills__item {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  // width: 120px;
  // min-width: 20px;
  // height: 120px;
  margin-bottom: 10px;
  padding: 10px;

  border-radius: 50%;

  &:hover {
    transform: scale(1.1);
  }

  @media (min-width: $tablet) {
    min-width: 120px;
  }
}

.skills__icon {
  display: flex;
  justify-content: center;
  width: 60px;
  height: 50px;

  text-align: center;

  &--webpack {
    padding-left: 10px;
  }

  &--gulp {
    padding-left: 18px;
  }
}

.skills__ch {
  margin: 0;
  margin-top: 10px;

  font-size: 1em;

  opacity: 0.3;
}

.skills__descr {
  margin: 0;
  margin-top: 35px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.skills__text {
  margin: 30px;
}
