.transition-loader {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;

  width: 100%;
  height: 100%;

  background-color: rgb(204, 204, 204);
}

.transition-loader--hide {
  display: none;
}

.transition-loader-inner {
  position: absolute;
  top: 50%;
  // width: calc(100% - 200px);
  // padding: 0 100px;

  width: 100%;

  text-align: center;
  color: #ffffff;

  transform: translateY(-50%);
  & label {
    display: inline-block;
    padding: 5%;

    font-size: 1em;

    border-radius: 50%;
    opacity: 0;
    &::after {
      content: "";
      position: absolute;

      width: 1em;
      height: 1em;

      background-color: #ffffff;
      border-radius: 50%;

      // font-family: FontAwesome;
    }

    @media (min-width: $tablet) {
      padding: 1%;
    }
  }
}

$key-frames-ms: 600ms, 500ms, 400ms, 300ms, 200ms, 100ms;

@each $current-ms in $key-frames-ms {
  $i: index($key-frames-ms, $current-ms);
  .transition-loader-inner label:nth-child(#{$i}) {
    -webkit-animation: loader 3s $current-ms infinite ease-in-out;
            animation: loader 3s $current-ms infinite ease-in-out;
  }
}

@keyframes loader {
  0% {
    transform: translateX(-300px) scale(1);
    opacity: 0;
  }
  33% {
    transform: translateX(0px) scale(2);
    opacity: 1;
  }
  66% {
    transform: translateX(0px) scale(1);
    opacity: 1;
  }
  100% {
    transform: translateX(300px) scale(2);
    opacity: 0;
  }
}

@-webkit-keyframes loader {
  0% {
    -webkit-transform: translateX(-300px);
    opacity: 0;
  }
  33% {
    -webkit-transform: translateX(0px);
    opacity: 1;
  }
  66% {
    -webkit-transform: translateX(0px);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(300px);
    opacity: 0;
  }
}
